<template>
    <div class="container">
        <h2>Iniciar Sessión</h2>
        <form v-on:submit.prevent="login">
            <div class="row">
                <div class="input-field col s12">
                    <input type="email" id="email" v-model="auth.email"/>
                    <label for="email">Email</label>
                </div>
                <div class="input-field col s12">
                    <input type="password" id="password" v-model="auth.password"/>
                    <label for="password">Password</label>
                </div>
                <button class="btn waves-effect waves-light blue darken-1" type="submit" name="action">Registrate
                </button>
            </div>
        </form>
    </div>
</template>

<script >
 export default {
     name: 'Login',
     data() {
         return {
             auth: {
                 email: '',
                 password: ''
             },
         }
     },
     methods: {
         login() {
             console.log(this.auth)
             this.$chat.post('/login', this.auth)
                    .then(res => {
                        if (res.data.iserror) {
                            alert(res.data.message)
                        }else {
                            // no error
                            alert(res.data.message)
                            this.$cookies.set("Token", res.data.data.token)
                            this.$router.push('/dashboard')
                        }
                    })
         }
     }
 }
</script>
