<template>
    <div>
        <Chat></Chat>
        Dashboard
        <div>{{ user.id }}</div>
        <div>{{ user.name  }}</div>
        <div>{{ user.email  }}</div>
        <div>{{ user.role  }}</div>
        <button v-on:click="closeSession">Cerrar sessión</button>
    </div>
</template>

<script >
 import Chat from './Chat.vue'
 export default {
     name: 'Dashboard',
     components: { Chat },
     data() {
         return {
             user: {}
         }
     },
     methods: {
         closeSession() {
             this.$cookies.remove('Token')
             this.$router.push('/')
         }
     },
     created() {
         var Token = this.$cookies.get('Token')
         if (Token === null) {
             this.$router.push('/')
             return
         }
         this.$chat.defaults.headers.common['Token'] = Token
         this.$chat.get('/users/me')
         .then(res => this.user = res.data)
     }
 }
</script>
