<script>
 //import Contactos from './components/Contactos.vue'
 import {uuid} from 'vue-uuid'

 export default {

     name: 'App',
     components: {
   //      Contactos
     },
     data() {
         return {
             contactos: [
                 {id: 0, name: 'Roberto', mensajes: []},
                 {id: 1, name: 'Carlos', mensajes: []},
                 {id: 2, name: 'Daniel', mensajes: []},
                 {id: 3, name: 'Peluchina', mensajes: []},
                 {id: 4, name: 'Pepe rata', mensajes: []}
             ],
             chat: {},
             enable: false,
             mensaje: '',
         }
     },
     methods: {
         setChat(id) {
             this.chat = this.contactos[id]
             this.enable = true
         },
         insertarmsg() {
             const newmensaje = {
                 id: uuid.v4(),
                 mensaje: this.mensaje
             }
             this.chat.mensajes.push(newmensaje)
             this.mensaje = ''
         }
     }
 }
